/** ---------------------------------------- redux ajax ---------------------------------------- */

/**
 * @desc 
 * @Params {params}
 * roomId : id
 */
export const getRoomByID = (params) => {
    return async dispatch => {
        try {
            let url = '/info/video/live/getRoomByID'
            const data = await axiosAjax({
                type: 'post',
                url: url,
                params: params,
                noLoading: true
            })
            if (data.code === 1) {
                dispatch({
                    type: GETROOMBYID,
                    data: data.obj
                })
            }
            return data
        } catch (err) {
            dispatch({
                type: ACTIONERROR,
                data: err
            })
            throw new Error(err)
        }
    }
}

