/**
 * @desc 
 * @Params {params} 
 * recommendFlag 0 1= 
 * passportId ID ID
 * pageSize 20
 * page 1
 * liveType  10000  1.()
 * displayFlag  1 0.
 * adminCreateFlag   1   0=
 * main  PC
 */
export const getRoomLiveList = (params) => {
    return async dispatch => {
        try {
            let url = '/info/video/live/getRoomListPage'
            const data = await axiosAjax({
                type: 'post',
                url: url,
                params: params,
                noLoading: true
            })
            if (data.code === 1) {
                if (params.recommendFlag) {
                    dispatch({
                        type: GETROOMLIVERECOMMENDLIST,
                        data: data.obj
                    })
                } else if (params.liveType) {
                    dispatch({
                        type: GETROOMLIVETYPELISTOBJDATA,
                        data: data.obj,
                        liveType: params.liveType
                    })
                } else if (params.main) {
                    dispatch({
                        type: GETROOMLIVENOWMAINLIST,
                        data: data.obj,
                        liveType: params.liveType
                    })
                } else {
                    dispatch({
                        type: GETROOMLIVELIST,
                        data: data.obj
                    })
                }
            }
            return data
        } catch (err) {
            dispatch({
                type: ACTIONERROR,
                data: err
            })
            throw new Error(err)
        }
    }
}

