/**
 * @desc 
 * @Params {params}
 * castId : id
 * orderBy  String asc/desc
 * refreshTime Long 13
 * pageSize
 */
export const getLiveList = (params, rank) => {
    return async dispatch => {
        try {
            let url = '/push/text/room/content/list'
            const data = await axiosAjax({
                type: 'post',
                url: url,
                params: params
            })
            if (data.code === 1) {
                if (rank) {
                    dispatch({
                        type: LISTRANK,
                        data: data.data
                    })
                } else {
                    dispatch({
                        type: GETLIVELIST,
                        data: data.data
                    })
                }
            }
            return data
        } catch (err) {
            dispatch({
                type: ACTIONERROR,
                data: err
            })
            throw new Error(err)
        }
    }
}

