/**
 * @desc / 
 * @Params {params}
 * passportId id
 * currentPage 
 * pageSize 
 * status  0:; 1: 2:; -1: 4:10
 */
export const getAuthorShowcolumnlist = (params) => {
    return async dispatch => {
        try {
            let url = '/info/news/showcolumnlist'
            const data = await axiosAjax({
                type: 'post',
                url: url,
                params: Object.assign({
                    status: 1,
                    passportId: '',
                    token: '',
                    currentPage: 1,
                    pageSize: 10
                }, params),
                noLoading: true
            })
            if (data.code === 1) {
                dispatch({
                    type: GETUSERCENTERAUTHORSHOWCOLUMNLIST,
                    data: data.obj
                })
            }
            return data
        } catch (err) {
            dispatch({
                type: ACTIONERROR,
                data: err
            })
            throw new Error(err)
        }
    }
}

