import React, { Component } from 'react'
import loadable from '@loadable/component'
import { mixUrl } from 'multiPublic/index'

const NstApp = loadable(() => import('../../containers-m/app/NstApp'))

export default class NstPage extends Component {
    static async getInitialProps (context) {
        const { match } = context
        let tdk = {
            title: '24',
            description: 'MarsBit',
            keywords: 'coinmarketcap'
        }
        return {
            clientLink: mixUrl.main(match.url),
            ...tdk
        }
    }

    render () {
        return <NstApp {...this.props}/>
    }
}
