import React, { Component } from 'react'
import loadable from '@loadable/component'
import { mixUrl } from 'multiPublic/index'

const Download = loadable(() => import('../containers/Download'))

export default class DownloadPage extends Component {
    static async getInitialProps (context) {
        const { match, req } = context
        let tdk = {
            title: 'MarsBit',
            keywords: `MarsBit,MarsBitMarsBitiOSMarsBitMarsBitAndroid`,
            description: 'MarsBit7x24MarsBit'
        }
        return {
            clientLink: mixUrl.m(match.url, req),
            ...tdk
        }
    }

    render () {
        return <Download {...this.props} />
    }
}
