import { axiosAjax } from 'multiPublic/index'
import {
    GETACTIVITYLIST,
    GETACTIVITYRECOMMENDLIST,
    GETACTIVITYPLACELIST,
    GETACTIVITYINFODATA
} from '../constants/activity'
import { ACTIONERROR } from 'multiRedux/constants/public'

/** ---------------------------------------- pure ajax ---------------------------------------- */
/**

/** ---------------------------------------- redux ajax ---------------------------------------- */

/**
 * @desc 
 * @Params {params} params = {passportids}
 * place:  all:; :; :overseas ; :others
 * timeType: :1:; 2: ;3:
 * recommend: 1: ; 0 :
 * currentPage: 
 * pageSize: 20
 */
export const getActivityList = (params) => {
    return async dispatch => {
        try {
            let url = '/info/activity/newlist'
            const data = await axiosAjax({
                type: 'post',
                url: url,
                params: params,
                noLoading: true
            })
            if (data.code === 1) {
                if (params.recommend === 1) {
                    dispatch({
                        type: GETACTIVITYRECOMMENDLIST,
                        data: data.obj
                    })
                } else {
                    dispatch({
                        type: GETACTIVITYLIST,
                        data: data.obj
                    })
                }
            }
            return data
        } catch (err) {
            dispatch({
                type: ACTIONERROR,
                data: err
            })
            throw new Error(err)
        }
    }
}

